@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
img {
  display: block;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: none;
  white-space: nowrap;
  clip: rect(0, 0, 0, 0);
  overflow: hidden;
  appearance: none;
}
.button {
  display: grid;
  align-items: center;
  justify-items: center;
  font-family: "Open Sans", "Arial", sans-serif;
  font-style: normal;
  text-decoration: none;
  border: none;
  background-color: transparent;
  cursor: pointer;
}
.button--basic {
  padding: 14px;
  line-height: 20px;
  font-weight: 700;
  color: #444444;
  text-transform: uppercase;
  text-decoration: none;
  border: 2px solid #444444;
}
@media (max-width: 767.9px) {
  .button--basic {
    font-size: 18px;
  }
}
@media (min-width: 768px) {
  .button--basic {
    font-size: 17px;
  }
}
.button--basic:focus,
.button--basic:hover {
  color: #ffffff;
  background-color: #444444;
}
.button--basic:focus:not(:focus-visible) {
  color: #444444;
  background-color: #ffffff;
}
.button--basic:focus:not(:focus-visible):hover {
  color: #ffffff;
  background-color: #444444;
}
.button--basic:focus:not(:focus-visible):active {
  color: rgba(255, 255, 255, 0.3);
  background-color: #444444;
}
.button--basic:focus-visible {
  outline: none;
  color: #ffffff;
  background-color: #444444;
}
.button--basic:active {
  color: rgba(255, 255, 255, 0.3);
  background-color: #444444;
}
.container {
  display: grid;
}
@media (max-width: 767.9px) {
  .container {
    grid-template-columns: 1fr 9% 82% 9% 1fr;
  }
}
@media (min-width: 768px) and (max-width: 1149.9px) {
  .container {
    grid-template-columns: 1fr 6.5% 87% 6.5% 1fr;
  }
}
@media (min-width: 1150px) and (max-width: 1549px) {
  .container {
    grid-template-columns: 1fr 4.5% 91% 4.5% 1fr;
  }
}
@media (min-width: 1550px) {
  .container {
    grid-template-columns: 1fr 62px 1426px 62px 1fr;
  }
}
.container--inner {
  grid-column: 1 / -1;
}
.page {
  margin: 0;
  padding: 0;
}
.page__body {
  display: grid;
  min-height: 100vh;
  margin: 0;
  padding: 0;
